import {delay} from '../../utils/utils.js'
import CircleButton from '../../components/CircleButton.vue'
// 进度条一个周期的时间
const COUNT_TIME = 20000
export default {
	data() {
		return {
			play: false,
			finish: false,
			best: 0,
			timer: null,
			timeSec: 0,
			startTime: 0,
			percent: 0
		};
	},
	computed: {
		toggleIcon() {
			return this.play ? 'icon-stop' : 'icon-play'
		}
	},
	onShow() {
		const best = uni.getStorageSync('best')
		if(best) {
			this.best = best
		}
	},
	methods: {
		// 切换计时模式
		toggleMode() {
			if(this.playing) {
				return
			}
			uni.redirectTo({
			  url: '../index/index'
			})
		},
		// 开始/停止按钮
		togglePlay() {
			if(this.play) {
				this.finish = true
				this.play = false
			} else {
				this.play = true
				this.finish = false
				this.startTime = Date.now()
				this.startCount()
			}
		},
		// 开始计时
		async startCount() {
			if(this.finish || !this.play) return
			await delay(15)
			this.updateCount()
			this.startCount()
		},
		updateCount() {
			const disMs = Date.now() - this.startTime
			const disSec = Math.floor(disMs / 1000)
			this.timeSec = disSec
			const countMs = disMs % COUNT_TIME
			if(countMs < COUNT_TIME/2) {
				this.percent = (countMs / (COUNT_TIME/2)) * 100
			} else {
				this.percent = 100 - ((countMs - (COUNT_TIME/2)) / (COUNT_TIME/2)) * 100
			}
		}
	},
	components: {
		CircleButton
	}
}
